    padding-bottom: 20px;
    padding-left: 20px;
    padding-right: 20px;
    margin-bottom: 40px;
}

.services button {
    font-family: 'Fredoka';
    
}

.service-options {
    display: flex;
    flex-direction: row;
    margin-top: -15px;
    
}

.styled-option input {
    position: absolute;
    cursor: pointer;
    width: 100%;
    max-width: 200px;
    opacity: 0;
    
}



.styled-option {
    display: inline-flex;
    align-content: center;
    align-items: center;
}

.checkmark {
    height: 17px;
    width: 17px;
    background: #FFFBF4;
    border-radius: 50px;
}
.styled-option input:checked ~ button {
    background: #5C1E1B;
}

.styled-option input:checked ~ button h5 {
    color: #7EC7B5;
}
.styled-option input:checked ~ button .checkmark {
    background-color: #5C1E1B;
    width: 5px;
    height: 10px;
    border: solid #7EC7B5;
    border-width: 0 3px 3px 0;
    border-radius: 0px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    position: relative;
    bottom: 2px;
    margin-right: 8px;
}

